import React from 'react';
import PropTypes from 'prop-types';
import serialize from 'serialize-javascript';
import config from '../config';

/* eslint-disable react/no-danger */

class Html extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    styles: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string.isRequired,
        cssText: PropTypes.string.isRequired,
      }).isRequired,
    ),
    scripts: PropTypes.arrayOf(PropTypes.string.isRequired),
    app: PropTypes.object, // eslint-disable-line
    children: PropTypes.string.isRequired,
    hostname: PropTypes.string,
  };

  static defaultProps = {
    styles: [],
    scripts: [],
    hostname: null,
  };

  assetsMap(asset) {
    if (asset.startsWith('/assets/') && this.props.hostname) {
      const domain = this.props.hostname.split('.');
      if (domain[domain.length - 2]) {
        return `//assets.${domain[domain.length - 2]}.${
          domain[domain.length - 1]
        }${asset}`;
      }
      return `//assets.${domain[domain.length - 1]}${asset}`;
    }
    return asset;
  }

  render() {
    const { title, description, styles, scripts, app, children } = this.props;
    return (
      <html className="no-js" lang="en">
        <head>
          <meta charSet="utf-8" />
          <meta httpEquiv="x-ua-compatible" content="ie=edge" />
          <title>{title}</title>
          <meta name="description" content={description} />
          <meta
            name="viewport"
            content="width=device-width, initial-scale = 1.0, maximum-scale=1.0, user-scalable=no"
          />
          <meta name="apple-mobile-web-app-capable" content="yes" />
          {scripts.map(script => (
            <link
              key={script}
              rel="preload"
              href={this.assetsMap(script)}
              as="script"
            />
          ))}
          <link rel="apple-touch-icon" href="apple-touch-icon.png" />
          {styles.map(style => (
            <style
              key={style.id}
              id={style.id}
              dangerouslySetInnerHTML={{ __html: style.cssText }}
            />
          ))}
        </head>
        <body>
          <div id="app" dangerouslySetInnerHTML={{ __html: children }} />
          <script
            dangerouslySetInnerHTML={{ __html: `window.App=${serialize(app)}` }}
          />
          {scripts.map(script => (
            <script key={script} src={this.assetsMap(script)} />
          ))}
          {config.analytics &&
            config.analytics.googleTrackingId && (
              <script
                dangerouslySetInnerHTML={{
                  __html:
                    'window.ga=function(){ga.q.push(arguments)};ga.q=[];ga.l=+new Date;' +
                    `ga('create','${
                      config.analytics.googleTrackingId
                    }','auto');ga('send','pageview')`,
                }}
              />
            )}
          {config.analytics &&
            config.analytics.googleTrackingId && (
              <script
                src="https://www.google-analytics.com/analytics.js"
                async
                defer
              />
            )}
        </body>
      </html>
    );
  }
}

export default Html;



